








import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { mapGetters } from 'vuex';
import { NodeStatusDto } from '@/interface/nodeStatusDto';
import { TreeStructureResponse } from '@/interface/treeStructureResponse';
import { findChildrenByIDOrName } from '@/util/tree.util';
import Tree from '@/components/Tree.vue';

interface TreeTargetMark extends TreeStructureResponse {
  target: boolean;
  children: TreeTargetMark[];
}

@Component({
  components: {
    Tree,
  },
  computed: {
    ...mapGetters({
      nodeStatus: 'nodeStatus',
      tree: 'tree',
    }),
  },
})
export default class FilterTree extends Vue {

  @Prop() public nodeIDOrName!: string;
  public nodeStatus!: NodeStatusDto;
  public tree!: TreeStructureResponse;
  public currentTree: TreeStructureResponse | null = null;

  public created() {
    if (this.tree) {
      this.updateTreeValue(this.tree);
    }
  }

  get treeStructure(): TreeStructureResponse | null {
    if (this.nodeIDOrName === '') {
      return this.currentTree || null;
    } else {
      const node = findChildrenByIDOrName(
        this.currentTree,
        this.nodeIDOrName.toUpperCase(),
      ) as TreeTargetMark;
      if (!node) {
        return null;
      }
      node.target = true;
      return this.filterNonTragetBranchFromCurrentTree();
    }
  }

  @Watch('tree')
  private treeWatcher(
    newValue: TreeStructureResponse,
    oldValue: TreeStructureResponse,
  ) {
    this.updateTreeValue(newValue);
  }

  @Watch('nodeIDOrName')
  private nodeIDOrNameWatcher(newValue: string, oldValue: string) {
    this.updateTreeValue(this.tree);
  }

  private updateTreeValue(value: TreeStructureResponse) {
    this.currentTree = JSON.parse(JSON.stringify(this.tree));
  }

  private filterNonTragetBranchFromCurrentTree(): TreeStructureResponse | null {
    if (this.currentTree && this.isTargetBranch(this.currentTree as TreeTargetMark)) {
      this.filterNonTragetBranch(this.currentTree as TreeTargetMark);
      return this.currentTree;
    } else {
      return null;
    }
  }

  private filterNonTragetBranch(root: TreeTargetMark): TreeTargetMark {
    if (root.target) {
      return root;
    } else {
      const result = root.children.map((o) => this.isTargetBranch(o));
      const indexOfBranch = result.indexOf(true);
      const targetBranch = root.children[indexOfBranch];
      this.filterNonTragetBranch(targetBranch);
      root.children = [this.filterNonTragetBranch(targetBranch)];
      return root;
    }
  }

  private isTargetBranch(root: TreeTargetMark): boolean {
    if (root && root.target) {
      return true;
    } else {
      if (root && root.children) {
        let isTarget = false;
        for (const child of root.children) {
          isTarget = isTarget || this.isTargetBranch(child);
        }
        return isTarget;
      } else {
        return false;
      }
    }
  }
}
