





































































import { Component, Vue, Prop } from 'vue-property-decorator';
import { TreeStructureResponse } from '@/interface/treeStructureResponse';
import { NodeStatusDto } from '@/interface/nodeStatusDto';
import * as d3 from 'd3';
import { PathProperty } from '@/interface/pathProperty';
import { NodeProperty } from '@/interface/nodeProperty';

interface TreeStructureProperty extends TreeStructureResponse {
  children: TreeStructureProperty[];
  status: 'online' | 'offline';
}

@Component
export default class Tree extends Vue {
  @Prop() public treeStructure!: TreeStructureResponse;
  @Prop() public nodeStatus!: NodeStatusDto;
  private readonly nodeMargin = 400;

  get svgDimension(): { width: number; height: number } {
    return {
      width: this.containerWidth,
      height: this.containerHeight,
    };
  }

  private get containerWidth(): number {
    return this.hierarchyNode
      ? (this.hierarchyNode.height + 1) * this.nodeMargin
      : 0;
  }

  private get containerHeight(): number {
    return this.hierarchyNode ? this.hierarchyNode.leaves().length * 30 : 0;
  }

  private get contentWidth(): number {
    return this.hierarchyNode ? this.hierarchyNode.height * this.nodeMargin : 0;
  }

  private get contentHeight(): number {
    return this.hierarchyNode ? this.hierarchyNode.leaves().length * 30 : 0;
  }

  get treeStructureProperty(): TreeStructureProperty | null {
    if (this.treeStructure) {
      const tree = this.treeStructure as TreeStructureProperty;
      this.setStatusProperty(tree, 'online');
      this.nodeStatus.L1.forEach((o) => {
        let node: TreeStructureProperty | null = null;
        if (tree.id === o || tree.name === o) {
          node = tree;
        } else {
          node = this.findChildrenByIDOrName(tree, o);
        }
        if (node) {
          this.setStatusProperty(node, 'offline');
        }
      });
      this.nodeStatus.L2.forEach
      ((o) => {
        let node: TreeStructureProperty | null = null;
        if (tree.id === o || tree.name === o) {
          node = tree;
        } else {
          node = this.findChildrenByIDOrName(tree, o);
        }
        if (node) {
          this.setStatusProperty(node, 'offline');
        }
      });
      this.nodeStatus.circuit.forEach((o) => {
        let node: TreeStructureProperty | null = null;
        if (tree.id === o.CIRCUIT || tree.name === o.CIRCUIT) {
          node = tree;
        } else {
          node = this.findChildrenByIDOrName(tree, o.CIRCUIT);
        }
        if (node) {
          this.setStatusProperty(node, o.STATUS);
        }
      });
      return tree;
    } else {
      return null;
    }
  }

  get hierarchyNode(): d3.HierarchyNode<TreeStructureProperty> | null {
    return this.treeStructureProperty
      ? d3.hierarchy<TreeStructureProperty>(this.treeStructureProperty)
      : null;
  }

  get nodes(): NodeProperty[] {
    return this.treeDescendants.map((o) => {
      const nodeProperty: NodeProperty = {
        id: o.data.id,
        class: 'node',
        translation: `translate(${o.y}, ${o.x})`,
        text: o.data.name || '',
        x: 0,
      };

      nodeProperty.class += ` node-${o.data.name ? 'active' : 'inactive'}`;
      nodeProperty.class += ` node-${
        o.data.status === 'offline' ? 'down' : 'up'
      }`;

      if (o.children) {
        nodeProperty.x = -13;
        nodeProperty.class += ' node-text-before';
      } else {
        nodeProperty.x = 13;
        nodeProperty.class += ' node-text-after';
      }

      return nodeProperty;
    });
  }

  get paths(): PathProperty[] {
    return this.pathDescendants.map((o) => {
      const pathProperty: PathProperty = {
        diagonal: '',
        class: 'link',
      };
      pathProperty.diagonal = `M ${o.target.y} ${o.target.x} C ${(o.target.y +
        o.source.y) /
        2} ${o.target.x}, ${(o.target.y + o.source.y) / 2} ${o.source.x}, ${
        o.source.y
      } ${o.source.x}`;

      pathProperty.class += ` link-${
        o.target.data.status === 'offline' ? 'down' : 'up'
      }`;

      return pathProperty;
    });
  }

  private get treeDescendants(): Array<
    d3.HierarchyPointNode<TreeStructureProperty>
  > {
    return this.hierarchyNode
      ? d3
          .tree<TreeStructureProperty>()
          .size([this.contentHeight, this.contentWidth - 200])(
            this.hierarchyNode,
          )
          .descendants()
      : [];
  }

  private get pathDescendants(): Array<
    d3.HierarchyPointLink<TreeStructureProperty>
  > {
    return this.hierarchyNode
      ? d3
          .tree<TreeStructureProperty>()
          .size([this.contentHeight, this.contentWidth - 200])(
            this.hierarchyNode,
          )
          .links()
      : [];
  }

  private setStatusProperty(
    treeProperty: TreeStructureProperty,
    isOnline: 'online' | 'offline',
  ) {
    this.$set(treeProperty, 'status', isOnline);
    if (treeProperty.children) {
      treeProperty.children.forEach((child) =>
        this.setStatusProperty(child, isOnline),
      );
    }
  }

  private findChildrenByIDOrName(
    node: TreeStructureProperty | null,
    idOrName: string,
  ): TreeStructureProperty | null {
    if (node && (node.id === idOrName || node.name === idOrName)) {
      return node;
    } else if (node && node.children) {
      let result: TreeStructureProperty | null = null;
      for (let i = 0; result === null && i < node.children.length; i++) {
        result = this.findChildrenByIDOrName(node.children[i], idOrName);
      }
      return result;
    }
    return null;
  }
}
